const FormModel = {
  name: '',        // 专业课程名称
  comment: '',     // 专业课程描述
  createBy: '',    // 创建人
  orgid: '',       // 所属组织机构（即学院专业）id
  orgNameCol: '',  // 所属组织机构（即学院专业）名称
  creationDate: '' // 创建日期
}

export default FormModel



